
#app {
  position: relative;
  width: 100%;
  height: 100%;
  background: #f8f8f8;
  overflow: hidden;
}
#app .child-view {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.content {
  width: 100%;
  height: 100%;
  padding-top: 1.34rem;
  position: relative;
}
.container {
  height: 100%;
  overflow: auto;
  position: relative;
  -webkit-overflow-scrolling: touch;
}
#index {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

#indexHeader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  overflow: hidden;
}
#indexHeader header {
  display: block;
  position: relative;
  background-color: #00939c;
  color: #fff;
}
#indexHeader header .top-bar {
  position: relative;
  height: 44px;
  line-height: 44px;
}
#indexHeader header .top-bar .abs-l,
#indexHeader header .top-bar .abs-m,
#indexHeader header .top-bar .abs-r {
  position: absolute;
  top: 0;
  width: 44px;
  height: 100%;
}
#indexHeader header .top-bar .abs-l {
  left: 0;
  z-index: 1000;
}
#indexHeader header .top-bar .abs-m {
  left: 0;
  width: 100%;
  z-index: 999;
}
#indexHeader header .top-bar .abs-r {
  right: 0;
  z-index: 1000;
}
#indexHeader header .top-bar a {
  display: block;
  width: 100%;
  height: 100%;
  color: inherit;
  font-size: inherit;
}
#indexHeader nav {
  position: relative;
  height: 36px;
  line-height: 36px;
  background-color: #f4f5f6;
  border-bottom: 1px solid #ddd;
  padding-right: 40px;
}
#indexHeader nav .nav-ul {
  white-space: nowrap;
  overflow-x: auto;
  font-size: 0;
  -webkit-overflow-scrolling: touch;
}
#indexHeader nav .nav-ul::-webkit-scrollbar {
  width: 0;
  heihgt: 0;
}
#indexHeader nav .nav-ul a {
  display: inline-block;
  white-space: nowrap;
  font-size: 16px;
  height: 36px;
  line-height: 36px;
  padding: 0 10px;
  margin-left: 5px;
  color: #505050;
  -webkit-tap-highlight-color: rgba(0,0,0,0.3);
}
#indexHeader nav .nav-ul a.active {
  color: #00939c;
  border-bottom: 2px solid #00939c;
}
#indexHeader nav .nav-more {
  position: absolute;
  right: 0;
  top: 0;
}
#indexHeader nav .nav-more .btn-more {
  display: inline-block;
  width: 40px;
  height: 35px;
}
#indexHeader nav .nav-more .line {
  position: absolute;
  left: -10px;
  top: 0;
  display: inline-block;
  width: 10px;
  height: 100%;
}
#indexHeader .title{
  /* background: url(~@/assets/img/logo.png) no-repeat center center;
  background-size: 145px; */
  text-align: center;
  font-size: 24px;
}
.btn-search{
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 24px;
}
.btn-more{
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 16px;
}
.line{
  background: url(data:image/png;base64,) no-repeat center center;
  background-size: 10px 35px;
}















#swiper-box {
  width: 100%;
  height: 100%;
  padding-top: 80px;
  background: #fff;
}
.container .globalTip {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
}
.container .globalTip div {
  height: 32px;
  line-height: 34px;
  font-size: 14px;
  color: #2a90d7;
  background: rgba(213,233,247,0.9);
  text-align: center;
  display: none;
}
.container .globalTip div.requestFail {
  color: #f56767;
  background: #fbe9ef;
}
.container .mint-loadmore-top {
  height: 50px;
  line-height: 50px;
  font-size: 14px;
}
.container .pullLoading {
  width: 24px;
  height: 24px;
  vertical-align: middle;
  margin-top: -4px;
  margin-right: 3px;
}
.container .bottomLoad .loading {
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
  color: #999;
}
.container .bottomLoad .loading span {
  vertical-align: middle;
  display: inline-block;
  margin-right: 10px;
}
#detail {
  height: 100%;
}
#detail .myHeader {
  background-color: #fff;
  color: #333;
}
#article {
  width: 100%;
  position: relative;
}
#article .article-info {
  background: #fff;
  border-bottom: 1px solid #eee;
  padding: 10px;
  font-size: 12px;
}
#article .article-info .title {
  font-size: 18px;
  padding-bottom: 10px;
}
#article .article-video video {
  width: 100%;
}
#article .article-content {
  padding: 10px;
  font-size: 16px;
}
#article .article-content .content-html {
  overflow: hidden;
}
#article .article-content .content-html img {
  width: 100% !important;
  height: auto !important;
}
#article .article-content .content-html p {
  margin-bottom: 10px;
}
#article .article-content .content-html.content-close {
  height: 1000px;
}
#article .article-content .showMore {
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #00939c;
  margin-top: 20px;
